                ></table-page>
              </div>
              <div v-if="settlementProfileList[0].fundingSourceCode != 1">
                <span class="tip">{{ this.$t('BUC.revenueSettlement.netRevenue') }}</span>
                <table-page
                  id="table"
                  class="margin-top-16 table-page"
                  style="padding-bottom: 16px; margin-top: 20px"
                  size="small"
                  :has-pagination="false"
                  :isSearch="netRevenueIsSearch"
                  max-height="280"
                  :columns="netColumns"
                  :getTableData="getNetTableData"
                ></table-page>
                <span class="tip">{{ this.$t('BUC.revenueSettlement.liabilitySettlement') }}</span>
                <table-page
                  id="table"
                  class="margin-top-16 table-page"
                  style="padding-bottom: 16px; margin-top: 20px"
                  size="small"
                  :has-pagination="false"
                  :isSearch="liabilitySettlementIsSearch"
                  max-height="280"
                  :columns="bottomLiabilityColumns"
                  :getTableData="getLiabilityData"
                ></table-page>
              </div>
            </div>
          </div>
        </div>
        <div class="margin-bottom-16 margin-top-20">
          <div class="detailInfo">
            <div class="desc" style="margin-bottom: 20px">{{ $t('BUC.revenueSettlement.settlementPlan') }}</div>
            <span class="tip">{{ $t('BUC.revenueSettlement.basicInfo') }}</span>
            <basic-info-item
              :itemInfoList="settlementPlanInfo"
              class="margin-top-20 bacicInfo needborder"
            ></basic-info-item>
            <span class="tip">{{ $t('BUC.revenueSettlement.settlementEntries') }}</span>
            <table-page
              id="table"
              class="margin-top-16 table-page"
              style="padding-bottom: 16px; margin-top: 20px"
              size="small"
              :has-pagination="false"
              :isSearch="settlementIsSearch"
              max-height="280"
              :columns="settlementColumns"
              :getTableData="getSettlementTableData"
            ></table-page>
          </div>
        </div>
        <div class="margin-bottom-16 margin-top-20">
          <div class="detailInfo" style="padding-bottom: 10px">
            <div class="desc" style="margin-bottom: 20px">{{ $t('BUC.commissionRollDown.taskRecord') }}</div>
            <div class="collapseBox bacicInfo">
              <el-collapse v-model="activeNames">
                <el-collapse-item v-for="(item, index) in collapseData" :key="index" :name="index + 1">
                  <template slot="title">
                    <span class="big-title">{{ item.commentId }} - {{ item.taskName }}</span>
                  </template>
                  <div v-for="(itemTwo, index) in item.children" :key="index">
                    <el-collapse-item class="record">
                      <template slot="title">
                        <span class="taskId">{{ itemTwo.creatorId }} - {{ itemTwo.creatorName }}</span>
                        <span class="taskAction">{{ itemTwo.action }}</span>
                        <span class="recordTime">{{ itemTwo.createTime }}</span>
                      </template>
                      <div class="recordContent">{{ itemTwo.commentText }}</div>
                    </el-collapse-item>
                  </div>
                </el-collapse-item>
              </el-collapse>
            </div>
          </div>
        </div>
      </div>
      <div class="panel-right">
        <span class="process-title">{{ this.$t('BUC.commissionRollDown.addComment') }}</span>
        <el-form :model="approveProcessForm" ref="approveProcessFormRef" label-position="top" size="small">
          <el-form-item label="Comment" prop="comment">
            <el-input type="textarea" v-model="approveProcessForm.comment" :rows="3"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" class="add-btn" plain @click="addComments" :disabled="addButtonDisable">{{
              $t('common.button.add')
            }}</el-button>
          </el-form-item>
          <span class="process-title">{{ this.$t('BUC.commissionRollDown.taskProcess') }}</span>
          <el-form-item
            class="radios"
            label="Action"
            prop="actionRadio"
            :rules="[{ required: true, message: 'Action is required' }]"
          >
            <el-radio-group :require="true" v-model="approveProcessForm.actionRadio">
              <el-radio label="approve">{{ this.$t('BUC.revenueSettlement.approve') }}</el-radio>
              <el-radio label="reject">{{ this.$t('BUC.revenueSettlement.reject') }}</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item
            class="remarkValue"
            label="Remark"
            prop="remarkValue"
            :rules="[{ required: true, message: 'Remark is required' }]"
          >
            <div>
              <el-input
                type="textarea"
                v-model="approveProcessForm.remarkValue"
                :rows="3"
                style="margin-bottom: 40px"
              ></el-input>
            </div>
          </el-form-item>
          <div class="process-btn">
            <el-button id="cancel" class="cancel-button" @click="cancel()">{{ $t('common.button.cancel') }}</el-button>
            <el-button
              id="submitbtn"
              type="primary"
              v-preventReClick
              @click="submitProcessForm"
              :loading="submitLoading"
            >
              {{ $t('common.button.submit') }}
            </el-button>
          </div>
        </el-form>
      </div>
    </div>
  </div>
</template>

<script>
import PortalHeader from '@/components/portal-header';
import TablePage from '@/components/tablePage';
import BasicInfoItem from '../component/basic-info-item.vue';
import {
  getRevenueSettlementDetail,
  queryWorkflowDetail,
  batchProcess,
  getRevenueSettlementList,
} from '../api/revenue-settlement';
import { dateFormat } from '@/common/utils/date-time-utils.js';
import AmountUnit from '../utils/amount-utils';
export default {
  name: 'RollDownApprove',
  components: {
    PortalHeader,
    BasicInfoItem,
    TablePage,
  },
  data() {
    return {
      basicInfo: [
        {
          title: this.$t('BUC.commissionRollDown.createTime'),
          content: '-',
        },
        {
          title: this.$t('BUC.commissionRollDown.dueTime'),
          content: '-',
        },
        {
          title: this.$t('BUC.commissionRollDown.taskStatus'),
          content: '-',
        },
        {
          title: this.$t('BUC.commissionRollDown.initiator'),
          content: '-',
        },
        {
          title: this.$t('BUC.commissionRollDown.orderStatus'),
          content: '-',
        },
        {
          title: this.$t('BUC.commissionRollDown.workId'),
          content: '-',
        },
        {
          title: this.$t('BUC.revenueSettlement.orderChannel'),
          content: '-',
        },
        {
          title: this.$t('BUC.revenueSettlement.eventSourse'),
          content: '-',
        },
        {
          title: this.$t('BUC.revenueSettlement.eventSourceSystem'),
          content: '-',
        },
        {
          title: this.$t('BUC.revenueSettlement.receiver'),
          content: '-',
        },
        {
          title: this.$t('BUC.revenueSettlement.orderStartTime'),
          content: '-',
        },
      ],
      collapseData: [],
      submitLoading: false,
      accountIsSearch: false,
      revenueIsSearch: false,
      liabilityIsSearch: false,
      netRevenueIsSearch: false,
      liabilitySettlementIsSearch: false,
      settlementIsSearch: false,
      activeNames: [1],
      approveProcessForm: {
        actionRadio: '',
        comment: '',
        remarkValue: '',
        reject: '',
      },
      processArray: [],
      isSearch: false,
      options: [],
      taskInfo: [],
      getPreviousList: [],
      getProcessList: [],
      params: {},
      accountList: [],
      settlementProfileList: [{ fundingSourceCode: '1' }],
      settlementEntriesList: [],
      amountUnitList: [],
      processDTO: {},
      taskTypeNew: '',
      accountAmountLabel: '',
      targetBalanceLabel: '',
      triggerBalanceLabel: '',
      settlementAmountLabel: '',
      netColumnsGettingAmountLabel: '',
      settlementInfoAmountLabel: '',
    };
  },
  computed: {
    addButtonDisable() {
      let addButtonDisable = false;
      if (this.approveProcessForm.comment === '') {
        addButtonDisable = true;
      }
      return addButtonDisable;
    },
    titleConfig() {
      return {
        title: this.$t('BUC.revenueSettlement.processTitle'),
      };
    },
    settlementPlanInfo() {
      return [
        {
          title: this.$t('BUC.revenueSettlement.planId'),
          content: '-',
        },
        {
          title: this.$t('BUC.revenueSettlement.status'),
          content: '-',
        },
        {
          title: this.$t('BUC.revenueSettlement.settlementAccount'),
          content: '-',
        },
        {
          title: this.$t('BUC.revenueSettlement.organizationShortCode'),
          content: '-',
        },
        {
          title: this.$t('BUC.revenueSettlement.organizationName'),
          content: '-',
        },
        {
          title: this.settlementInfoAmountLabel,
          content: '-',
        },
      ];
    },
    settlementInfo() {
      return [
        {
          title: this.$t('BUC.revenueSettlement.fundingSourse'),
          content: '-',
        },
        {
          title: this.$t('BUC.revenueSettlement.settlementAccount'),
          content: '-',
        },
        {
          title: this.$t('BUC.revenueSettlement.currency'),
          content: '-',
        },
        {
          title: this.settlementInfoAmountLabel,
          content: '-',
        },
      ];
    },
    accountColumns() {
      return [
        {
          label: this.$t('BUC.revenueSettlement.settlementType'),
          prop: 'settlementTypeName',
          align: 'left',
          'sort-method': (a, b) => {
            return a.settlementType > b.settlementType ? 1 : -1;
          },
        },
        {
          label: this.$t('BUC.revenueSettlement.accountName'),
          prop: 'accountName',
          align: 'left',
        },
        {
          label: this.$t('BUC.revenueSettlement.accountNO'),
          prop: 'accountNo',
          align: 'left',
        },
        {
          label: this.$t('BUC.revenueSettlement.accountType'),
          prop: 'accountTypeName',
          align: 'left',
        },
        {
          label: this.$t('BUC.revenueSettlement.currency'),
          prop: 'currency',
          align: 'left',
        },
        { label: this.accountAmountLabel, prop: 'amount', align: 'left' },
      ];
    },
    revenueColumns() {
      return [
        {
          label: this.$t('BUC.revenueSettlement.revenueAccount'),
          prop: 'accountTypeName',
          align: 'left',
        },
        {
          label: this.targetBalanceLabel,
          prop: 'targetBalance',
          align: 'left',
        },
        {
          label: this.triggerBalanceLabel,
          prop: 'triggerBalance',
          align: 'left',
        },
        {
          label: this.$t('BUC.revenueSettlement.maxBalanceTrigger'),
          prop: 'maxMinBalanceTriggerNew',
          align: 'left',
        },
      ];
    },
    liabilityColumns() {
      return [
        {
          label: this.$t('BUC.revenueSettlement.liabilityAccount'),
          prop: 'accountTypeName',
          align: 'left',
        },
        {
          label: this.targetBalanceLabel,
          prop: 'targetBalance',
          align: 'left',
        },
        {
          label: this.triggerBalanceLabel,
          prop: 'triggerBalance',
          align: 'left',
        },
        {
          label: this.$t('BUC.revenueSettlement.minBalanceTrigger'),
          prop: 'maxMinBalanceTriggerNew',
          align: 'left',
        },
      ];
    },
    settlementColumns() {
      return [
        {
          label: this.$t('BUC.revenueSettlement.settlementType'),
          prop: 'settlementTypeName',
          align: 'left',
        },
        {
          label: this.$t('BUC.revenueSettlement.reasonType'),
          prop: 'reasonTypeName',
          align: 'left',
        },
        {
          label: this.$t('BUC.revenueSettlement.debitAccount'),
          prop: 'debitAccountTypeName',
          align: 'left',
        },
        {
          label: this.$t('BUC.revenueSettlement.creditAccount'),
          prop: 'creditAccountTypeName',
          align: 'left',
        },
        {
          label: this.settlementAmountLabel,
          prop: 'amount',
          align: 'left',
        },
        {
          label: this.$t('BUC.revenueSettlement.status'),
          prop: 'statusName',
          align: 'left',
        },
        {
          label: this.$t('BUC.revenueSettlement.errorMessage'),
          prop: 'errorMsg',
          align: 'left',
        },
      ];
    },
    netColumns() {
      return [
        {
          label: this.$t('BUC.revenueSettlement.accountType'),
          prop: 'accountTypeName',
          align: 'left',
        },
        {
          label: this.netColumnsGettingAmountLabel,
          prop: 'gettingAmount',
          align: 'left',
        },
        {
          label: this.targetBalanceLabel,
          prop: 'triggerBalance',
          align: 'left',
        },
      ];
    },
    bottomLiabilityColumns() {
      return [
        {
          label: this.$t('BUC.revenueSettlement.accountType'),
          prop: 'accountTypeName',
          align: 'left',
        },
        {
          label: this.targetBalanceLabel,
          prop: 'targetBalance',
          align: 'left',
        },
        {
          label: this.triggerBalanceLabel,
          prop: 'triggerBalance',
          align: 'left',
        },
        {
          label: this.$t('BUC.revenueSettlement.minimumBalanceTrigger'),
          prop: 'maxMinBalanceTriggerNew',
          align: 'left',
        },
      ];
    },
  },
  created() {
    this.params = JSON.parse(decodeURIComponent(this.$route.query.params));
    this.getProcessInfo();
    this.queryWorkflowDetail();
    this.setSettlementPlanInfo();
    if (this.params.statusName === 'Second Settlement Failed') {
      this.taskTypeNew = 'NetHandle';
    } else if (this.params.statusName === 'First Settlement Failed') {
      this.taskTypeNew = 'GrossHandle';
    } else if (this.params.statusName === 'Prepared') {
      this.taskTypeNew = 'Process';
    }
  },
  methods: {
    async getProcessInfo() {
      let data = { planOrderId: this.params.planOrderId };
      let res = await getRevenueSettlementDetail(data);
      if (res.header && res.header.success) {
        this.accountList = res.accountList;
        this.accountIsSearch = true;
        this.settlementEntriesList = res.entryList;
        this.settlementIsSearch = true;
        this.settlementProfileList = res.profile.settlementProfileList;
        this.revenueIsSearch = true;
        this.liabilityIsSearch = true;
        this.netRevenueIsSearch = true;
        this.liabilitySettlementIsSearch = true;
        this.setSettlementInfo();
      }
    },
    async queryWorkflowDetail() {
      let data = {
        processId: this.params.flowId || '',
        taskName: 'CommissionTask',
      };
      let res = await queryWorkflowDetail(data);
      if (res.header && res.header.success) {
        this.processDTO = res.processDTO;
        this.processDTO.orderStartTime = dateFormat(this.processDTO.createTime, 'DD/MM/YYYY HH:mm:ss');
        this.processDTO.taskState = res.taskDTO.taskState;
        this.processDTO.initiatorName = res.taskDTO.initiatorName;
        this.processDTO.creationTime = res.taskDTO.createTime;
        this.processDTO.dueTime = res.taskDTO.expireTime;
        this.processDTO.eventSourceName = this.showEventSource(res.processDTO.eventSource);
        this.setBasicInfo();
        let obj = {};
        let noPid = [];
        this.collapseData = [];
        if (res.comments) {
          res.comments.forEach((item) => {
            if (!item.pid) {
              noPid.push(item);
            }
          });
          noPid.forEach((item) => {
            obj[item.commentId] = item;
            obj[item.commentId].taskName = item.taskName || '';
            obj[item.commentId].children = [];
          });
          res.comments.forEach((item) => {
            if (item.pid) {
              obj[item.pid].children.push(item);
            }
          });
          for (let key in obj) {
            this.collapseData.push(obj[key]);
          }
        }
      }
    },
    setBasicInfo() {
      this.basicInfo[0].content = this.processDTO.createTime || '-';
      this.basicInfo[1].content = this.processDTO.expireTime || '-';
      this.basicInfo[2].content = this.processDTO.taskState || '-';
      this.basicInfo[3].content = this.processDTO.initiatorName || '-';
      this.basicInfo[4].content = this.processDTO.procStatus || '-';
      this.basicInfo[5].content = this.processDTO.processId || '-';
      this.basicInfo[6].content = this.processDTO.channel || '-';
      this.basicInfo[7].content = this.processDTO.eventSourceName || '-';
      this.basicInfo[8].content = this.processDTO.eventSourceSystem || '-';
      this.basicInfo[9].content = this.processDTO.receiverName || '-';
      this.basicInfo[10].content = this.processDTO.orderStartTime || '-';
    },
    showEventSource(val) {
      switch (val) {
        case '1':
          return 'Porta';
        case '2':
          return 'AG';
        case '3':
          return 'ThirdParty';
        case '4':
          return 'System';
        case '5':
          return 'NC';
        case '6':
          return 'TransactionCenter';
        case '7':
          return 'IdentityBusiness';
        case '8':
          return 'BulkCenter';
        default:
          return;
      }
    },
    setSettlementPlanInfo() {
      this.settlementPlanInfo[0].content = this.params.planOrderId || '-';
      this.settlementPlanInfo[1].content = this.params.statusName || '-';
      this.settlementPlanInfo[2].content = this.params.settlementAccountTypeName || '-';
      this.settlementPlanInfo[3].content = this.params.shortCode || '-';
      this.settlementPlanInfo[4].content = this.params.orgName || '-';
      this.settlementPlanInfo[5].content = this.params.expectedNetAmount || '-';
    },
    setSettlementInfo() {
      this.settlementInfo[0].content = this.settlementProfileList[0].fundingSourceValue || '-';
      this.settlementInfo[1].content = this.settlementProfileList[0].settlementAccountTypeName || '-';
      this.settlementInfo[2].content = this.settlementProfileList[0].currency || '-';
      this.settlementInfo[3].content = this.settlementProfileList[0].settlementId || '-';
    },
    getAccountTableData() {
      let symbol = AmountUnit.format('100', this.accountList[0].currency);
      let symbolFirst = symbol.substring(0, 1);
      this.accountAmountLabel = `${this.$t('BUC.revenueSettlement.tableAmount')}(${symbolFirst})`;
      return new Promise((resolve) => {
        this.accountIsSearch = false;
        let res = {
          header: {
            success: true,
          },
          list: this.accountList,
        };
        resolve(res);
      });
    },
    getRevenueTableData() {
      let symbol = AmountUnit.format('100', this.settlementProfileList[0].currency);
      let symbolFirst = symbol.substring(0, 1);
      this.targetBalanceLabel = `${this.$t('BUC.revenueSettlement.targetBalance')}(${symbolFirst})`;
      this.triggerBalanceLabel = `${this.$t('BUC.revenueSettlement.triggerBalance')}(${symbolFirst})`;
      this.netColumnsGettingAmountLabel = `${this.$t('BUC.revenueSettlement.gettingAmount')}(${symbolFirst})`;
      this.settlementInfoAmountLabel = `${this.$t('BUC.revenueSettlement.amount')}(${symbolFirst})`;
      return new Promise((resolve) => {
        this.revenueIsSearch = false;
        this.settlementProfileList[0].grossRevenueList.forEach((item) => {
          if (item.maxMinBalanceTrigger) {
            item.maxMinBalanceTriggerNew = 'Able';
          } else {
            item.maxMinBalanceTriggerNew = 'Disabled';
          }
        });
        let res = {
          header: {
            success: true,
          },
          list: this.settlementProfileList[0].grossRevenueList,
        };
        resolve(res);
      });
    },
    getLiabilityTableData() {
      return new Promise((resolve) => {
        this.liabilityIsSearch = false;
        this.settlementProfileList[0].grossLiabilityList.forEach((item) => {
          if (item.maxMinBalanceTrigger) {
            item.maxMinBalanceTriggerNew = 'Able';
          } else {
            item.maxMinBalanceTriggerNew = 'Disabled';
          }
        });
        let res = {
          header: {
            success: true,
          },
          list: this.settlementProfileList[0].grossLiabilityList,
        };
        resolve(res);
      });
    },
    getNetTableData() {
      let symbol = AmountUnit.format('100', this.settlementProfileList[0].currency);
      let symbolFirst = symbol.substring(0, 1);
      this.targetBalanceLabel = `${this.$t('BUC.revenueSettlement.targetBalance')}(${symbolFirst})`;
      this.triggerBalanceLabel = `${this.$t('BUC.revenueSettlement.triggerBalance')}(${symbolFirst})`;
      this.netColumnsGettingAmountLabel = `${this.$t('BUC.revenueSettlement.gettingAmount')}(${symbolFirst})`;
      this.settlementInfoAmountLabel = `${this.$t('BUC.revenueSettlement.amount')}(${symbolFirst})`;
      return new Promise((resolve) => {
        this.netRevenueIsSearch = false;
        let res = {
          header: {
            success: true,
          },
          list: this.settlementProfileList[0].revenueList,
        };
        resolve(res);
      });
    },
    getLiabilityData() {
      return new Promise((resolve) => {
        this.liabilitySettlementIsSearch = false;
        this.settlementProfileList[0].liabilityList.forEach((item) => {
          if (item.maxMinBalanceTrigger) {
            item.maxMinBalanceTriggerNew = 'Able';
          } else {
            item.maxMinBalanceTriggerNew = 'Disabled';
          }
        });
        let res = {
          header: {
            success: true,
          },
          list: this.settlementProfileList[0].liabilityList,
        };
        resolve(res);
      });
    },
    getSettlementTableData() {
      let symbol = AmountUnit.format('100', this.settlementEntriesList[0].currency);
      let symbolFirst = symbol.substring(0, 1);
      this.settlementAmountLabel = `${this.$t('BUC.revenueSettlement.tableAmount')}(${symbolFirst})`;
      return new Promise((resolve) => {
        this.settlementIsSearch = false;
        let res = {
          header: {
            success: true,
          },
          list: this.settlementEntriesList,
        };
        resolve(res);
      });
    },
    async addComments() {
      if (this.approveProcessForm.comment) {
        let data = {
          actionType: 'addcomments',
          comments: this.approveProcessForm.comment,
          isBatch: false,
          processList: [
            {
              processId: this.params.flowId,
              taskType: this.taskTypeNew,
            },
          ],
        };
        let res = await batchProcess(data);
        if (res.header && res.header.success) {
          this.$message.success('Comment added successfully.');
          this.$refs.approveProcessFormRef.resetFields();
          this.queryWorkflowDetail();
        }
      }
    },
    cancel() {
      this.$confirm('Are you sure to cancel all data you have entered/selected in this page ?', 'Confirm', {
        confirmButtonText: 'Yes',
        cancelButtonText: 'No',
        closeOnClickModal: false,
        type: 'warning',
        customClass: 'box-logout',
      }).then(() => {
        this.$refs['approveProcessFormRef'].resetFields();
      });
    },
    async submitProcessForm() {
      this.submitLoading = true;
      this.$refs['approveProcessFormRef'].validate(async (valid) => {
        if (!valid) return;
        let data = {
          actionType: this.approveProcessForm.actionRadio,
          comments: this.approveProcessForm.remarkValue,
          isBatch: false,
          processList: [
            {
              processId: this.params.flowId,
              taskType: this.taskTypeNew,
            },
          ],
        };
        let res = await batchProcess(data);
        this.submitLoading = false;
        if (res.header && res.header.success) {
          this.$refs.approveProcessFormRef.resetFields();
          this.queryWorkflowDetail();
          this.$message.success('Revenue Settlement reviewed Succeeded.');
          let query = {
            spQuery: false,
            planId: this.params.planOrderId,
            pageNo: 1,
            pageSize: 1,
          };
          let res2 = await getRevenueSettlementList(query);
          if (res2.header && res2.header.success) {
            if (
              res2.list[0].statusName === 'First Settlement Failed' ||
              res2.list[0].statusName === 'Second Settlement Failed'
            ) {
              this.params = res2.list[0];
              this.getProcessInfo();
              this.queryWorkflowDetail();
              this.setSettlementPlanInfo();
              if (this.params.statusName === 'Second Settlement Failed') {
                this.taskTypeNew = 'NetHandle';
              } else if (this.params.statusName === 'First Settlement Failed') {
                this.taskTypeNew = 'GrossHandle';
              } else if (this.params.statusName === 'Prepared') {
                this.taskTypeNew = 'Process';
              }
            } else {
              this.$router.push({
                path: 'settlementDetail',
                query: { params: JSON.stringify(res2.list[0]) },
              });
            }
          }
        }
      });
    },
  },
};
</script>

